.skills-page {
    padding: 20px;
  }
  
  .skills-section {
    margin-bottom: 40px;
  }
  
  .skills-section h2 {
    font-size: 1.8rem;
    margin-bottom: 20px;
    text-align: center;
    color: #333;
  }
  
  .skills-container {
    display: flex;
    flex-wrap: wrap;
    gap: 30px;
    justify-content: center;
    padding: 10px;
  }
  
  .skill-button {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px;
    width: 150px; /* Tamanho aumentado */
    height: 60px; /* Tamanho aumentado */
    border: 2px solid transparent;
    border-radius: 12px;
    background-color: #f0f0f0;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.2s ease;
    font-size: 1.1rem;
    font-weight: bold;
  }
  
  .skill-button:hover {
    background-color: var(--hover-color);
    transform: scale(1.05);
  }
  
  .skill-name {
    text-align: center;
  }
  
  .game-demo-section {
    text-align: center;
    padding: 40px 20px;
    background-color: #fafafa;
    border-top: 2px solid #ddd;
  }
  
  .game-demo-placeholder {
    margin-top: 20px;
    padding: 50px;
    background-color: #e0e0e0;
    border-radius: 10px;
    color: #555;
    font-size: 1.2rem;
  }
  