.projects {
  padding: 80px 20px;
  background: linear-gradient(135deg, #2c2c54, #1e1e2f);
  color: #e6e6ff;
}
.projects h2 {
  font-size: 2.5rem;
  color: #9c88ff;
}
.projects-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
  margin-top: 40px;
}
.project-card {
  background: #40407a;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s ease;
}
.project-card h3 {
  color: #f8a5c2;
}
.project-link {
  text-decoration: none;
  color: #00d8d6;
  font-weight: bold;
  display: inline-block;
  margin-top: 15px;
}
.project-link:hover {
  color: #e1b12c;
}
