.header {
  background-color: #2c2c54;
  padding: 20px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
}
.header nav ul {
  list-style-type: none;
  display: flex;
  justify-content: space-around;
  margin: 0;
  padding: 0;
}
.header nav ul li {
  font-size: 1.2rem;
}
.header nav ul li a {
  text-decoration: none;
  color: #e6e6ff;
  transition: color 0.3s ease;
}
.header nav ul li a:hover {
  color: #f0a500;
}
